.img {
  display: block;
  width: 50%;
  margin-left: 5px;
  -ms-user-select: none;
  -moz-user-select: none;
  -khtml-user-select: none;
  -webkit-user-select: none;
  -o-user-select: none;
  user-select: none;
}

.inputFile {
  display: block;
  margin-left: 5px;
  margin-top: 5px;
  margin-bottom: 5px;
}

.box {
  position: absolute;
  border: 4px solid black;
  background-color: none;
  cursor: move;
}

.faceBox {
  position: absolute;
  border: 4px dashed blue;
  background-color: transparent;
}
